.content {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  .mb40 {
    margin-bottom: 40px;
  }
  .team-block {
    margin-bottom: 20px;
    
  }
  .team-img {
    position: relative;
  }
  .team-img img {
    width: 100%;
    height: 15vmax ;
    min-height: 250px;
    object-fit: cover;
  }
  .team-meta {
    color: #9da4aa;
    font-weight: 400;
    font-size: 16px;
  }
  .overlay {
    border-radius: 0px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: 1s ease;
    background-color: #11181f;
  }
  .team-img:hover .overlay {
    opacity: 0.8;
  }
  
  .text {
    color: #fff;
    position: absolute;
    top: 30%;
    left: 30%;
    transform: translate(-26%, -26%);
    -ms-transform: translate(-26%, -26%);
    right: 0;
    
  }
  