.btn-whatsapp {
    display:block;   
    height:80px;
    color:#fff;
    position: fixed;
    right:20px;
    bottom:20px;
    border-radius:50%;    
    line-height:80px;
    text-align:center;
    z-index:1;        
}

.insta{
    color: rgb(209, 89, 89);
}

.face{
    color: rgb(81, 152, 218);
}
.content {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  .mb40 {
    margin-bottom: 40px;
  }
  .team-block {
    margin-bottom: 20px;
    
  }
  .team-img {
    position: relative;
  }
  .team-img img {
    width: 100%;
    height: 15vmax ;
    min-height: 250px;
    object-fit: cover;
  }
  .team-meta {
    color: #9da4aa;
    font-weight: 400;
    font-size: 16px;
  }
  .overlay {
    border-radius: 0px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: 1s ease;
    background-color: #11181f;
  }
  .team-img:hover .overlay {
    opacity: 0.8;
  }
  
  .text {
    color: #fff;
    position: absolute;
    top: 30%;
    left: 30%;
    -webkit-transform: translate(-26%, -26%);
            transform: translate(-26%, -26%);
    -ms-transform: translate(-26%, -26%);
    right: 0;
    
  }
  
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

